.hero_container--left h2 {
    font-size: 4rem;
    color: #222121;
}

@media screen and (max-width: 280px) {
    .hero_container--left h2 {
        font-size: 2rem;
    }
}

.hero_container--left p {
    font-size: 2rem;
    color: #222121;
    margin-top: 1rem;
    font-weight: 700;
}

@media screen and (max-width: 280px) {
    .hero_container--left p {
        font-size: 2rem;
    }
}

.hero_container--btn {
    font-size: 1rem;
    background-color: #00cc66;
    padding: 14px 32px;
    border: none;
    border-radius: 4px;
    color: #f7f7f7;
    margin-top: 2rem;
    cursor: pointer;
    position: relative;
    transition: all 0.3s;
    outline: none;
}

.hero_container--btn a {
    position: relative;
    z-index: 2;
    color: #f7f7f7;
    text-decoration: none;
}

.hero_container--btn:after {
    position: absolute;
    content: '';
